___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Staatliches', cursive;
}

.header {
    width: 100%;
    padding: 20px;
    background-color: var(--primary-color);
}

.header h1 {
    font-size: 2rem;
}

.container-principal {
    display: grid;
    grid-template-columns: 1fr 4fr;
    width: 100%;
    height: 100vh;
}

.container-principal aside {
    border: black solid 2px;
}

.container-principal aside ul {
    display: flex;
    flex-direction: column;
}

.link {
    display: block;
    text-align: center;
    text-decoration: none;
    color: black;
    font-family: 'Patua One', cursive;
    font-family: 'Staatliches', cursive;
    background-color: var(--white-color);
    font-size: 1rem;
    letter-spacing: 2px;
    width: 100%;
    padding: 20px;
}

.link:hover {
    background-color: var(--primary-color);
    cursor: pointer;
}

.clicado {
    background-color: var(--primary-color);
}

.coitaner-function {
    display: flex;
    justify-content: center;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,WAAW;IACX,aAAa;IACb,sCAAsC;AAC1C;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,qBAAqB;IACrB,YAAY;IACZ,iCAAiC;IACjC,mCAAmC;IACnC,oCAAoC;IACpC,eAAe;IACf,mBAAmB;IACnB,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,sCAAsC;IACtC,eAAe;AACnB;;AAEA;IACI,sCAAsC;AAC1C;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    font-family: 'Staatliches', cursive;\n}\n\n.header {\n    width: 100%;\n    padding: 20px;\n    background-color: var(--primary-color);\n}\n\n.header h1 {\n    font-size: 2rem;\n}\n\n.container-principal {\n    display: grid;\n    grid-template-columns: 1fr 4fr;\n    width: 100%;\n    height: 100vh;\n}\n\n.container-principal aside {\n    border: black solid 2px;\n}\n\n.container-principal aside ul {\n    display: flex;\n    flex-direction: column;\n}\n\n.link {\n    display: block;\n    text-align: center;\n    text-decoration: none;\n    color: black;\n    font-family: 'Patua One', cursive;\n    font-family: 'Staatliches', cursive;\n    background-color: var(--white-color);\n    font-size: 1rem;\n    letter-spacing: 2px;\n    width: 100%;\n    padding: 20px;\n}\n\n.link:hover {\n    background-color: var(--primary-color);\n    cursor: pointer;\n}\n\n.clicado {\n    background-color: var(--primary-color);\n}\n\n.coitaner-function {\n    display: flex;\n    justify-content: center;\n}"],"sourceRoot":""}]);
